import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

//import { Link } from 'react-router-dom';
//dependencies
import * as SerieServer from "../services/serie";
import * as TemporadaServer from "../services/temporada";
import * as VideoServer from "../services/videoServer";
import * as HistorialUserServer from "../services/historialUser";

import SeriesList from "../components/videos/series";
import VideoLoadingComponent from "../components/videos/videoLoading";
//components
import { Container } from "@material-ui/core";
import { LazyLoadImage } from "react-lazy-load-image-component";
import 'react-lazy-load-image-component/src/effects/blur.css'

/**
 * Carga todos los videos que sean 'Series' para el modulo Series de la aplicaci√≥n
 */
function AppSeries() {
  const VideoLoading = VideoLoadingComponent(SeriesList);
  const history = useNavigate();
  const [histUsers, setHistUsers] = useState();
  // 
  const [series, setSeries] = useState([]);
  const [videos, setVideos] = useState([]);
  const [idSerie, setIdSerie] = useState(0);
  const [temporadas, setTemporadas] = useState([]);
  const [idTemporada, setIdTemporada] = useState(0);
  const [appState, setAppState] = useState({
    loading: true,
    videos: null,
  });
  const [videoCounts, setVideoCounts] = useState({});

  useEffect(() => {
    const getSeries = async () => {
      const res = await SerieServer.ListSeries();
      const series = res;
      setSeries(series);
      setAppState({ loading: false });
    };
    getSeries();
  }, []);

  useEffect(() => {
    const getTemporadas = async () => {
      const res = await TemporadaServer.ListTemporadas();
      const temporadas = res;
      setTemporadas(temporadas);
    };
    getTemporadas();
  }, []);

  useEffect(() => {
    const fetchVideoCounts = async () => {
      const counts = {};
      for (const serie of series) {
        counts[serie.id] = await getTotalVideosSerie(serie.id);
      }
      setVideoCounts(counts);
    };

    if (series.length > 0) {
      fetchVideoCounts();
    }
  }, [series]);

  const getHistorialUsers = async () => {
    try {
      const res = await HistorialUserServer.ListHistorial();
      setHistUsers(res);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getHistorialUsers();
  }, []);

  /**
   * Funci√≥n para redireccionar al reproductor del video seleccionado, se crea un nuevo historial de usuario si es la primera vez en ver el video, de lo contrario se env√≠a este historial como state
   * @param {*} id
   */
/*   const entrarVideo = async (id) => {
    const formData = new FormData();
    let histUser;
    let contador = 0;
    formData.append("usuario", user.id);
    formData.append("video", id);
    try {
      for (let index = 0; index < histUsers.length; index++) {
        const element = histUsers[index];
        if (element.usuario.id === user.id && element.video === id) {
          histUser = element;
          contador++;
        }
      }
      if (contador === 0) {
        const hu = await HistorialUserServer.RegisterHistorialUser(formData);
        histUser = hu.data;
      }
    } catch (error) {
      console.log(error);
    }
    history(`/seeVideo/${id}`, { state: histUser });
  }; */

  const entrarTemporada = (temporadaId) => {
    history(`/seeTemporada/${temporadaId}`, { state: histUsers });
    setIdTemporada(temporadaId);
  };
  
  const entrarSerie = (serieId) => {
    history(`/seeSerie/${serieId}`, { state: histUsers });
    setIdSerie(serieId);
  };

  const getTotalVideosSerie = async (idSerie) => {
    const res = await VideoServer.ListVideos();
    console.log(res);
    const videos = res.videos.filter(video => video.tipe_of_video == 2 && video.serie == idSerie && video.state == 1);
    console.log(videos);
    return videos.length;
  };

  return (
    <div className="d-flex justify-content-center">
      <div style={{ maxWidth: "1450px", minWidth: "50%", padding: "20px" }}>
        {appState.loading ? <VideoLoading isLoading={appState.loading} series={series} /> : ""}
        <div className="d-flex justify-content-end">
          <button onClick={() => window.history.back()} className="btn btn-light" value="Volver">Volver</button>
        </div>
        <h2 className="px-2 mb-3">Listado de Series</h2>
        <div className="d-flex flex-wrap">
          {series.map((serie, index) => {
            return (
              <div className="border rounded cursorPointer card-width p-2 m-3" key={index}>
                <div onClick={() => { entrarSerie(serie.id) }}>
                  {videoCounts[serie.id] > 0 &&
                  <div className="d-flex justify-content-end mb-1"><small> {videoCounts[serie.id] || ''} Videos</small></div>
                  }
                  <LazyLoadImage effect="blur" src={'https://sccotview.com' + serie.featured_image} className="rounded mb-2" alt="" />
                  <h5 className="text-capitalize truncate-text">{serie.serie}</h5>
                  <small className="truncate-text mb-0">{serie.description}</small>
                </div>
                <div>
                  {temporadas.map((temporada, index) => {
                    if (serie.id === temporada.serie) {
                      return (
                        <div key={index}>
                          {/* Aqu√≠ puedes agregar m√°s contenido de la temporada si es necesario */}
                        </div>
                      );
                    }
                    return null;
                  })}
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}

export default AppSeries;
