import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {  useContext } from "react";

//components
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { makeStyles } from "@material-ui/core/styles";
import Carousel from "react-bootstrap/Carousel";

//dependencies
import VideosItem from "./videosItem";
import VideosCategoriaFila2 from "./videosCategoriaFila2";
import VideosCategoriaFila from "./videosCategoriaFila";
import * as HistorialUserServer from "../../services/historialUser";
import * as PreferenciasUserServer from "../../services/preferenciasUser";
import Context from "../context/UserContext";
import VideoListSearch from "./videoListSearch";

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 1248,
    maxHeight:460,
    margin: "auto",
  },
  "@media (max-width: 1248px)": {
    root: {
      display: "flex",
    },
  },
}));

/**
 * Componente para organizar en las pesta√±as (videos, series, casos) los videos en su respectiva categoria, con un buscador y filtro
 * @param {*} videos
 * @param {*} categories
 * @returns Contenedor con los videos y categorias 
 */
function VideosListUser2({videos, categories, ...props}) {
  const history = useNavigate();

  const [histUsers, setHistUsers] = useState();
  const [prefUsers, setPrefUsers] = useState(null); 
  const [categoriasTotales, setCategoriasTotales] = useState(null);
  const { user } = useContext(Context)

  /**
   * Funci√≥n para obtener el historial de los usuarios.
   */
  const getHistorialUsers = async () => {
    try {
      const res = await HistorialUserServer.ListHistorialUser({'user_id':user.id});
      setHistUsers(res);
    } catch (error) {
      console.log(error);
    }
  };
  /**
   * Funci√≥n para obtener las preferencias de los usuarios
   */
  const getPreferenciasUser = async () => {
    try {
      const res = await PreferenciasUserServer.ListPreferenciaUser({'user_id':user.id});   
      setPrefUsers(res);
    } catch (error) {
      console.log(error);
    }
  };
  
  useEffect(() => {
    getHistorialUsers();
    getPreferenciasUser();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  
  /**
   * Funci√≥n para organizar las categorias de videos dependiendo de las preferencias.
   */
  useEffect(() => {

    if(prefUsers && categories) {
      let array = [];
      let array1 =[];
      let array2 = [];

      for (let i = 0; i < prefUsers.length; i++) {
        array1.push(prefUsers[i].categoria);        
      } 
      for (let i = 0; i < categories.length; i++) {
        array2.push(categories[i]);        
      }    
      
      for (var i = 0; i < array2.length; i++) {
          var igual=false;
           for (var j = 0; j < array1.length & !igual; j++) {
               if(array2[i].categoria === array1[j] )
                igual=true;
              }
              array = prefUsers
          if(!igual)array.push(array2[i]);
      }
      
      if (array) {
        setCategoriasTotales(array);
      }
      else {
        setCategoriasTotales(null);
      }
      
    }
  },[categories, prefUsers])
  /**
   * Funci√≥n para redireccionar al reproductor del video seleccionado, se crea un nuevo historial de usuario si es la primera vez en ver el video, de lo contrario se env√≠a este historial como state
   * @param {*} id 
   */
  const verVideo = async (id) =>{
    const formData = new FormData();
    let histUser;
    let contador = 0;
    formData.append("usuario", user.id);
    formData.append("video", id);
    try {
      for (let index = 0; index < histUsers.length; index++) {
      const element = histUsers[index];
        if (element.usuario.id === user.id && element.video === id) {
          histUser= element;
          contador++;
        }
      }
      if(contador ===0) {
        const hu = await  HistorialUserServer.RegisterHistorialUser(formData);
        histUser = hu.data;
      }
    } catch (error) {
      console.log(error);
    }
    history(`/seeVideo/${id}`,{state:histUser});
    
  }
  
  const classes = useStyles();
  if (videos.length > 0 ) {
    return (
      <div>
        <div id="carousel_videos">
          {/* {console.log(props.listVideos)}
          {console.log(videos)} */}
         
          <h2>üïë √öltimos videos</h2>
          <Carousel className={classes.root} id="carousel" fade >
            {videos &&
              props.search(videos).slice(0, 6).map((video,index) => (
                <Carousel.Item key={index}>
                  <VideosItem
                    key={index}
                    video={video}
                    listVideos={props.listVideos}
                    histUsers = {histUsers}
                    user = {user}
                    verVideo = {verVideo}
                  />
                </Carousel.Item>
              ))}
          </Carousel>
        </div>
        <br></br>
        <hr></hr>
        <div id="card_videos">
          {
          prefUsers ? 
            <div >
            
              {categoriasTotales && props.search2(categoriasTotales).map((element, index) => (
                <>
                {props.search2(categoriasTotales).length <= 1 ?
                 <VideoListSearch 
                 key= {index}
                 categoria ={element}
                 videos={videos}
                 search={props.search}
                 histUsers = {histUsers}
                 user = {user}
                 verVideo= {verVideo}
                 >
 
                 </VideoListSearch>
                :
                <VideosCategoriaFila
                  key= {index}
                  categoria ={element}
                  videos={videos}
                  search={props.search}
                  histUsers = {histUsers}
                  user = {user}
                  verVideo= {verVideo}  
                  >
                </VideosCategoriaFila>
                }
                </>
                ))}
            </div>
          :
          categories && props.search2(categories).map((element,index) => (
            <div key={index}>
            {props.search2(categories).length <= 1 ?
            <VideoListSearch 
            key= {index}
            categoria ={element}
            videos={videos}
            search={props.search}
            histUsers = {histUsers}
            user = {user}
            verVideo= {verVideo}
            >

            </VideoListSearch>
            :
              <VideosCategoriaFila2
                key= {index}
                categoria ={element}
                videos={videos}
                search={props.search}
                histUsers = {histUsers}
                user = {user}
                verVideo= {verVideo}
              >
              </VideosCategoriaFila2>
            }
              </div>
            ))
          }
        </div>
      </div>
    );
  }
  else {
    return (
      <div id="card_videos">
        <h2 style={{color:"white"}}><center>No hay videos en este modulo!!</center></h2>
      </div>
    )
  }
}

export default VideosListUser2;
